@font-face {
  font-family: 'WalsheimReg';
  src: local('WalsheimReg'), url(fonts/Web/GT-Walsheim-Pro-Condensed-Regular.woff2) format('woff2');
}
@font-face {
  font-family: 'WalsheimBold';
  src: local('WalsheimBold'), url(fonts/Web/GT-Walsheim-Pro-Condensed-Black.woff2) format('woff2');
}
@font-face {
  font-family: 'WalsheimCR';
  src: local('WalsheimCR'), url(fonts/Web/GT-Walsheim-Pro-Condensed-Regular.woff2) format('woff2');
}
@font-face {
  font-family: 'ZirconBook';
